import { InputNumber, Modal, notification } from 'antd';
import React, { useEffect, useRef } from 'react';
import { useSelector } from 'react-redux';

export default function ModalCash({ state, handleState, handleCancel, handlePaymentList }) {
	const ref = useRef(null);

	const { data: transaction = [] } = useSelector((state) => state?.transaction);
	const { tender = {}, modalCash = false, settles = [] } = state;

	const handleOk = (e) => {
		if (transaction.length === 0) {
			return notification.error({
				message: 'Perhatian!',
				description: 'Tidak ada transaksi, silahkan lakukan transaksi terlebih dahulu.'
			});
		}

		if (+state?.cashPaid === 0 || +state?.cashPaid < 0) {
			return notification.error({
				message: 'Perhatian!',
				description: 'Nominal pembayaran tidak boleh 0 atau minus'
			});
		}

		if (+tender?.paidType === 2) {
			ref.current.blur();
			e.target.blur();
			handleState('modalCard', true);
			handleState('modalCash', false);
		}

		handlePaymentList(state?.tender);
		handleCancel();
	};

	const handleFocus = () => {
		if (ref && ref.current !== null) {
			ref.current.focus();
			ref.current.select();
		}
	};

	useEffect(() => {
		handleFocus();
	}, []);

	useEffect(() => {
		handleFocus();
	}, [modalCash, tender, settles]);

	return (
		<Modal open={modalCash} onOk={handleOk} onCancel={handleCancel} centered width={650}>
			<h1 className="text-lg font-semibold text-center mb-5">INPUT NOMINAL PEMBAYARAN</h1>

			<InputNumber
			min={0}
				name="cashPaid"
				ref={ref}
				autoFocus
				className="w-full h-32 p-5 flex items-center bg-transparent text-4xl text-black font-bold border-none focus:outline-none"
				placeholder="MASUKKAN NOMINAL PEMBAYARAN"
				value={state?.cashPaid}
				formatter={(value) => `IDR ${value}`.replace(/\B(?=(\d{3})+(?!\d))/g, ',')}
				parser={(value) => value?.replace(/IDR\s?|(,*)/g, '')}
				onChange={(e) => {
					handleState('cashPaid', e);
				}}
				onPressEnter={(e) => {
					handleOk(e);
				}}
			/>
		</Modal>
	);
}
