import React, { useEffect, useRef, useState } from 'react';
import { PrintPage } from '../PrintPage';

import { Form, notification } from 'antd';
import { useDispatch, useSelector } from 'react-redux';
import { useHistory } from 'react-router-dom/cjs/react-router-dom.min';
import { useReactToPrint } from 'react-to-print';
import actions from '../../../redux/transaction/actions';
import { currencyFormat } from '../../../utils/helper/utilities';
import '../app.css';
import ModalCashChange from '../components/ModalCashChange';
import usePayment from '../hooks/usePayment';
import DetailBill from './widget/DetailBill';
import ModalCard from './widget/ModalCard';
import ModalCash from './widget/ModalCash';
import TableBalanceOrder from './widget/TableBalanceOrder';
import TenderMedia from './widget/TenderMedia';

export default function PaymentPage() {
	const history = useHistory();
	const dispatch = useDispatch();

	let componentPrintRef = useRef(null);
	// const paidInputRef = useRef(null);

	const { billNo = '', data = [] } = useSelector((state) => state?.transaction);
	const { getSubtotal, getGrandTotal, getCashPaid, getCashChange, getBalanceDue, paymentStore } = usePayment();
	const { cashier = {} } = JSON.parse(localStorage.getItem('app') || '{}');

	const [form] = Form.useForm();
	const [state, setState] = useState({
		cashPaid: 0,
		cashChange: 0,
		card: {
			name: '',
			no: ''
		},
		settles: [],
		subtotal: getSubtotal(),
		grandTotal: getGrandTotal(),
		modalCash: false,
		modalCard: false,
		modalCashChange: false,
		tender: {}
	});

	const openPageCashier = () => {
		dispatch({
			type: actions.SET_STATE,
			payload: {
				contentStatus: 'isMenu'
			}
		});

		return history.push('/pos/kasir');
	};

	const getTenderMedia = () => {
		dispatch({
			type: 'transaction/TENDER_MEDIA'
		});
	};

	const addPaymentList = (tender) => {
		const { settleCode = '', tenderName = '', paidType = '' } = tender;
		const settles = state?.settles || [];
		const cashChange = getCashChange(settles, state?.cashPaid);

		if (state?.cashPaid > 0) {
			const settle = {
				billNo,
				custCode: '',
				custName: '',
				paidType,
				settleCode,
				amount: getGrandTotal(),
				amountPaid: state?.cashPaid || 0,
				cashPay: state?.cashPaid || 0,
				cashChange: cashChange || 0,
				tenderName,
				cardName: +paidType === 2 ? state?.card.name : '',
				cardHolder: +paidType === 2 ? state?.card.name : '',
				cardNo: +paidType === 2 ? state?.card.no : ''
			};

			setState((prevState) => ({
				...prevState,
				settles: [...prevState.settles, settle],
				cashPaid: 0
			}));

			form.resetFields();
			return;
		}

		notification.error({
			message: 'Gagal!',
			description: 'Nominal yang anda masukkan tidak boleh 0.'
		});
	};

	const handleClickTender = (tender) => {
		const { paidType = 0 } = tender;

		// handleFocusInputCash();

		if (data?.length < 0) {
			return notification.warning({
				message: 'Perhatian!',
				description: 'Anda tidak dapat melakukan pembayaran jika tidak ada item yang dibeli.'
			});
		}

		setState((prev) => ({
			...prev,
			tender
		}));

		if (+paidType === 1) {
			setState((prevState) => ({
				...prevState,
				modalCash: true
			}));
			return;
		}

		if (+paidType === 2) {
			setState((prevState) => ({
				...prevState,
				modalCash: true,
				cashPaid: getBalanceDue(state?.settles, state?.cashPaid)
			}));

			return;
		}

		if (+paidType >= 6 && +paidType < 8) {
			// FOR FOC
			notification.warning({
				message: 'Perhatian!!!',
				description: 'Fitur ini sedang dalam tahap pengembangan. Terima kasih'
			});

			return;
		}
	};

	// const handleFocusInputCash = () => {
	// 	if (paidInputRef && paidInputRef.current !== null) {
	// 		paidInputRef.current.focus();
	// 		paidInputRef.current.select();
	// 	}
	// };

	const handlePrint = useReactToPrint({
		content: () => componentPrintRef,
		onPrintError: (e) => console.log('Error Print!', e),
		onAfterPrint: () => {
			openPageCashier();
		}
	});

	const handlePaymentStore = () => {
		return paymentStore(
			state,
			handlePrint(null, () => componentPrintRef)
		);
	};

	const handlePayment = () => {
		const settles = state?.settles || [];
		const cashChange = getCashChange(settles, state?.cashPaid);
		const cashPaid = getCashPaid(settles);
		const grandTotal = getGrandTotal();

		if (cashPaid > grandTotal && cashChange > 0) {
			setState((prev) => ({
				...prev,
				modalCashChange: true
			}));

			return;
		}

		if (cashPaid === grandTotal) {
			handlePaymentStore();
			return;
		}
	};

	const handleKeyDown = (e) => {
		const key = e.key;

		if (key === 'F2') {
			// handleFocusInputCash();
		}
	};

	const handleState = (state, value) => {
		setState((prev) => ({
			...prev,
			[state]: value
		}));
	};

	const handleCancel = () => {
		// handleFocusInputCash();

		dispatch({
			type: 'transaction/SET_STATE',
			payload: {
				contentStatus: 'isMenu'
			}
		});

		setState((prev) => ({
			...prev,
			cashPaid: 0,
			modalCash: false,
			modalCard: false,
			modalCashChange: false
		}));
	};

	useEffect(() => {
		getTenderMedia();

		if (!cashier?.isOpening) {
			history.push('/pos');

			notification.error({
				message: 'Error!',
				description: 'Mohon untuk melakukan Buka Kasir terlebih dahulu sebelum melanjutkan transaksi.'
			});
		}
	}, []);

	useEffect(() => {
		// handleFocusInputCash();

		document.addEventListener('keydown', handleKeyDown);
		return () => {
			document.removeEventListener('keydown', handleKeyDown);
		};
	}, []);

	useEffect(() => {
		if (data?.length !== 0 && state?.settles.length !== 0) {
			handlePayment();
			return;
		}
	}, [state?.settles]);

	return (
		<div className="w-screen h-screen overflow-x-hidden overflow-y-scroll">
			<div className="w-full h-full grid grid-cols-12 grid-rows-6 gap-3 p-3">
				<div className="col-span-3 row-span-5 bg-white overflow-scroll p-3 rounded-md border-2 border-gray-400">
					<div className="w-full h-full">
						<PrintPage
							ref={(el) => (componentPrintRef = el)}
							transactions={data}
							cash={{
								cashChange: getCashChange(state?.settles, state?.cashPaid),
								cashPaid: getCashPaid(state?.settles),
								total: state?.grandTotal
							}}
							billDate={new Date()}
							payments={state?.settles}
							paymentMethod={state?.settles[0]?.tenderName}
						/>
					</div>
				</div>
				<div className="col-span-9 row-span-1 bg-primary rounded-md">
					<div className="w-full h-full p-5 flex flex-row items-center justify-between gap-3">
						<h1 className="text-lg text-white">Total Belanja</h1>
						<h1 className="text-4xl text-white font-semibold capitalize">Rp {currencyFormat(getGrandTotal(), 0)}</h1>
					</div>
					.
				</div>
				<div className="col-span-9 row-span-1">
					<div className="w-full h-full flex items-center gap-3">
						<div className="w-full h-full bg-white rounded-md p-3 flex flex-col items-center justify-center">
							<h1>Kembalian</h1>
							<h1 className="text-2xl font-semibold capitalize">
								IDR {currencyFormat(getCashChange(state?.settles, state?.cashPaid), 0)}
							</h1>
						</div>
						<div className="w-full h-full bg-white rounded-md p-3 flex flex-col items-center justify-center">
							<h1>Sisa Bayar</h1>
							<h1 className="text-2xl font-semibold capitalize">
								IDR {currencyFormat(getBalanceDue(state?.settles, state?.cashPaid), 0)}
							</h1>
						</div>
					</div>
				</div>
				{/* <div className="col-span-9 row-span-1">
					<div className="w-full h-full bg-gray-800 rounded-md overflow-hidden">
						<Form form={form} name="cash-paid-form">
							<Form.Item name="cashPaid" initialValue={0}>
								<InputNumber
									disabled={!cashier?.isOpening}
									name="cashPaid"
									ref={paidInputRef}
									autoFocus
									value={state?.cashPaid}
									formatter={(value) => `IDR ${value}`.replace(/\B(?=(\d{3})+(?!\d))/g, ',')}
									parser={(value) => value?.replace(/IDR\s?|(,*)/g, '')}
									onChange={(e) => {
										setState({
											...state,
											cashPaid: e
										});
									}}
									className="w-full h-96 p-5 flex items-center bg-transparent text-4xl text-white font-bold border-none focus:outline-none"
									onPressEnter={(e) => {
										handleFocusInputCash();
									}}
								/>
							</Form.Item>
						</Form>
					</div>
				</div> */}
				<div className="col-span-9 row-span-6">
					<div className="w-full h-full overflow-hidden grid grid-cols-2 gap-5">
						<div className="w-full h-full overflow-scroll">
							<TenderMedia form={form} state={state} setState={setState} handleClickTender={handleClickTender} handleFocus={() => {}} />
						</div>
						<div className="w-full h-full overflow-scroll">
							<TableBalanceOrder state={state} setState={setState} handleFocus={() => {}} />
						</div>
					</div>
				</div>
				<div className="col-span-3 row-span-4 bg-white border-2 border-gray-400 rounded-md overflow-scroll px-5 py-2">
					<DetailBill state={state} />
				</div>
			</div>

			<ModalCash
				state={state}
				handleState={handleState}
				handleCancel={handleCancel}
				handlePaymentList={addPaymentList}
				handleFocus={() => {}}
			/>
			<ModalCard state={state} setState={setState} handlePaymentList={addPaymentList} handleFocus={() => {}} />
			<ModalCashChange
				printRef={componentPrintRef}
				handlePaymentStore={handlePaymentStore}
				// handlePaymentStore={() => paymentStore(state, () => {})}
				handlePrint={() => handlePrint(null, () => componentPrintRef.current)}
				open={state?.modalCashChange}
				state={state}
				handleCancel={handleCancel}
			/>
		</div>
	);
}
