import { Button, Modal } from 'antd';
import React, { useEffect, useRef } from 'react';
import { currencyFormat } from '../../../utils/helper/utilities';
import usePayment from '../hooks/usePayment';

export default function ModalCashChange(props) {
	const modalRef = useRef();

	const { handlePaymentStore = (e) => {}, handlePrint = () => {}, state = {}, handleCancel = () => {}, open = false } = props;
	const { getCashChange } = usePayment();

	const handleEnterPress = (e) => {
		if (e.key === 'Enter') {
			e.preventDefault();
			handlePaymentStore();
		}
	};

	useEffect(() => {
		const modal = modalRef.current;

		if (open && modal) {
			modal.addEventListener('keydown', handleEnterPress);
			modal.focus();

			return () => {
				modal.removeEventListener('keydown', handleEnterPress);
			};
		}
	}, [open]);

	return (
		<Modal centered width={450} footer={false} onCancel={handleCancel} open={open} bodyStyle={{ backgroundColor: '#f8f8f8' }}>
			<div autoFocus={true} tabIndex={-1} ref={modalRef} className="w-full h-48 flex items-center justify-center flex-col gap-3">
				<h1 className="text-lg font-medium text-gray-700">KEMBALIAN PELANGGAN</h1>
				<p className="text-3xl font-bold">Rp {currencyFormat(getCashChange(state?.settles, state?.cashPaid), 0)}</p>

				<Button className="w-1/2 btn-page h-14 mt-7" onClick={handlePrint}>
					PRINT BILL
				</Button>
			</div>
		</Modal>
	);
}
