import { useDispatch, useSelector } from 'react-redux';

export default function usePayment() {
	const dispatch = useDispatch();

	const { billNo = '', billDate = '', data = [] } = useSelector((state) => state?.transaction);
	const { cashier = {}, property = {} } = JSON.parse(localStorage.getItem('app') || '{}');

	const getTotalQty = () => data?.reduce((acc, { qty = 0 }) => +acc + +qty, 0) || 0;

	const getSubtotal = () => data?.reduce((acc, { total = 0 }) => +acc + +total, 0) || 0;

	const getDiscountTotal = () => 0;

	const getGrandTotal = () => {
		return getSubtotal() - getDiscountTotal();
	};

	const getCashPaid = (settles = []) => {
		const totalCashPay =
			settles?.length !== 0
				? settles.reduce((acc, cur) => {
						return +acc + +cur.cashPay;
				  }, 0)
				: 0;

		return totalCashPay;
	};

	const getCashChange = (settles = [], cashPaid = 0) => {
		const grandTotal = getGrandTotal();
		const totalCashPay = getCashPaid(settles);

		if (grandTotal === 0) {
			return 0;
		}

		if (cashPaid > grandTotal) {
			return cashPaid - grandTotal;
		}

		if (totalCashPay > grandTotal) {
			return totalCashPay - grandTotal;
		}

		return 0;
	};

	const getBalanceDue = (settles = [], cashPaid = 0) => {
		const cashChange = getCashChange(settles, cashPaid);
		const totalCashPay = getCashPaid(settles);
		const grandTotal = getGrandTotal();

		if (totalCashPay >= grandTotal) {
			return totalCashPay - grandTotal - cashChange;
		}

		return getGrandTotal() - +totalCashPay;
	};

	const paymentStore = (state, handlePrint) => {
		const header = {
			billNo,
			billDate,
			custCode: '',
			custName: '',
			discPrc: '0',
			discValue: '0',
			taxPrc: '0',
			taxValue: '0',
			subTotal: getSubtotal(),
			grandTotal: getGrandTotal(),
			shiftID: cashier?.shiftId || '0',
			outletCode: property?.outletCode
		};

		const settle =
			state.settles?.length !== 0 &&
			state.settles?.map((settle) => {
				return {
					...settle,
					cashChange: getCashChange(state?.settles, settle?.cashPay)
				};
			});

		const payload = {
			header: [header],
			settle,
			details: data?.map((el, i) => ({ ...el, billNo, ItemNo: i + 1 }))
		};

		dispatch({
			type: 'transaction/STORE',
			payload,
			bill: {
				from: billDate,
				to: billDate
			},
			handlePrint: () => {
				if (typeof handlePrint === 'function') {
					return handlePrint();
				}
			}
		});
	};

	return { getTotalQty, getDiscountTotal, getSubtotal, getGrandTotal, getCashPaid, getCashChange, getBalanceDue, paymentStore };
}
