import { Button } from 'antd';
import React, { useEffect, useRef, useState } from 'react';
import { useSelector } from 'react-redux';
import { useHistory } from 'react-router-dom';
import usePayment from '../../hooks/usePayment';

export default function TenderMedia({ form, state, setState, handleClickTender, handleFocus }) {
	const history = useHistory();
	const { tenderMedia = [], data = [] } = useSelector((state) => state?.transaction);
	const { getBalanceDue, getGrandTotal } = usePayment();

	const ref = useRef();
	const buttonRefs = useRef([]);

	const [activeIndex, setActiveIndex] = useState(-1);

	const openPage = () => history.push('/pos/kasir');

	const handleCash = () => {
		const balanceDue = getBalanceDue(state?.settles, state?.cashPaid);

		setState((prev) => ({
			...prev,
			cashPaid: balanceDue
		}));

		form.setFieldValue('cashPaid', balanceDue);
		handleFocus();
	};

	const onKeyDownGlobal = (e) => {
		const key = e.key;

		if (key === 'F3') {
			// F3 key
			e.preventDefault();

			setActiveIndex(0);
			ref && ref.current.focus();
		}

		if (key === 'F4') {
			setActiveIndex(-1);
			ref && ref.current.blur();
		}

		if (key === 'Escape') {
			e.target.blur();
			handleFocus();
		}

		if (key === 'End') {
			openPage();
		}
	};

	const onKeyDown = (e) => {
		const key = e.key;
		const allowedKeys = ['ArrowUp', 'ArrowDown', 'ArrowLeft', 'ArrowRight', 'Enter'];

		if (allowedKeys.includes(key)) {
			e.preventDefault();

			let newIndex = activeIndex;
			const numCols = 3; // Number of columns in the grid

			if (key === 'ArrowLeft') {
				// Arrow Right
				if (activeIndex % numCols === 0) {
					// Stay on the same row if already on the leftmost column
					newIndex = activeIndex + (numCols + 1);
				} else {
					newIndex -= 1;
				}
			} else if (key === 'ArrowRight') {
				// Arrow Left
				if ((activeIndex + 1) % numCols === 0) {
					// Stay on the same row if already on the rightmost column
					newIndex = activeIndex - (numCols - 1);
				} else {
					newIndex += 1;
				}
			} else if (key === 'ArrowUp') {
				// Arrow Up
				if (activeIndex - numCols < 0) {
					// Stay on the same row if already on the topmost row
					newIndex = (tenderMedia.length - numCols + (activeIndex % numCols)) % tenderMedia.length;
				} else {
					newIndex -= 1;
				}
			} else if (key === 'ArrowDown') {
				// Arrow Down
				if (activeIndex + numCols >= tenderMedia.length) {
					// Stay on the same row if already on the bottommost row
					newIndex = (tenderMedia.length - numCols + (activeIndex % numCols)) % tenderMedia.length;
				} else {
					newIndex += 1;
				}
			} else if (key === 'Enter') {
				// Enter
				const isDisabled = buttonRefs.current[newIndex].disabled;

				if (!isDisabled) {
					handleClickTender(tenderMedia[activeIndex]);
				}
			}

			setActiveIndex(newIndex);
			buttonRefs.current[newIndex] && buttonRefs.current[newIndex].scrollIntoView({ behavior: 'smooth', block: 'center' });
		}
	};

	useEffect(() => {
		document.addEventListener('keydown', onKeyDownGlobal);

		if (data.length === 0) {
			openPage();
		}

		return () => {
			document.removeEventListener('keydown', onKeyDownGlobal);
		};
	}, []);

	useEffect(() => {
		const tenderDiv = ref && ref.current !== null ? ref.current : null;
		if (tenderDiv) {
			tenderDiv.addEventListener('keydown', onKeyDown);

			return () => {
				tenderDiv.removeEventListener('keydown', onKeyDown);
			};
		}
	}, [activeIndex, tenderMedia]);

	return (
		<>
			<div ref={ref} tabIndex={-1} id="tender-media" className="w-full h-full overflow-y-scroll overflow-x-hidden ">
				<h1 className="mb-3 text-lg font-semibold">
					Metode Pembayaran <kbd className="kbd">f3</kbd>
				</h1>
				<div className="w-full h-auto grid grid-cols-3 gap-3">
					<Button className="col-span-3 btn-page bg-transparent border-2 border-red-500 text-red-600" onClick={openPage}>
						batalkan <kbd className="kbd">end</kbd>
					</Button>
					{/* <Button className="col-span-2 btn-page" onClick={handleCash}>
						uang pas <kbd className="kbd">f1</kbd>
					</Button> */}

					{tenderMedia?.length !== 0 &&
						tenderMedia?.map((tender, index) => {
							const { settleCode, tenderName } = tender;

							const filteredTender =
								state?.settles.length !== 0 ? state?.settles.filter((settle) => settle?.settleCode === settleCode) : [];

							const isDisabled = filteredTender?.length !== 0 ? filteredTender[0].settleCode === settleCode : false;
							const disabledButton = isDisabled || data?.length === 0 || getGrandTotal() === 0;
							return (
								<Button
									ref={(el) => (buttonRefs.current[index] = el)}
									className={`btn-page ${index === activeIndex ? 'active' : ''}`}
									disabled={disabledButton}
									key={settleCode}
									onClick={() => {
										if (!disabledButton) {
											handleClickTender(tender);
											return;
										}

										return null;
									}}
									onKeyDown={(e) => {
										if (e.key === 'Enter') {
											if (!disabledButton) {
												handleClickTender(tender);
												return;
											}
										}
									}}>
									{tenderName.trim().toUpperCase()}
								</Button>
							);
						})}
				</div>
			</div>
		</>
	);
}
