import { Button, Form, Input, Modal } from 'antd';
import React, { useEffect, useRef } from 'react';

export default function ModalCard({ state, setState, handlePaymentList }) {
	const [form] = Form.useForm();

	const modalCardRef = useRef(null);
	const cardNameRef = useRef(null);
	const cardNumberRef = useRef(null);

	const handleCancel = () => {
		setState((prevState) => ({
			...prevState,
			modalCard: false,
			modalCash: false
		}));

		form.resetFields();
	};

	const onFinish = () => {
		handlePaymentList(state?.tender);
		handleCancel();
	};

	const onKeyDownGlobal = (e) => {
		if (e.key === 'ArrowUp') {
			handleFocusCardName();
		}

		if (e.key === 'ArrowDown' && cardNameRef.current && document.activeElement === cardNameRef.current.input) {
			cardNameRef && cardNameRef.current.blur();
			cardNumberRef && cardNumberRef.current.focus();
		}

		if (e.key === 'Enter' && cardNameRef.current && document.activeElement === cardNameRef.current.input) {
			cardNameRef && cardNameRef.current.blur();
			cardNumberRef && cardNumberRef.current.focus();
		}
	};

	const handleFocusCardName = () => {
		return cardNameRef && cardNameRef.current && cardNameRef.current.focus();
	};

	useEffect(() => {
		handleFocusCardName();

		const modal = modalCardRef && modalCardRef.current !== null;

		if (modal) {
			document.addEventListener('keydown', onKeyDownGlobal);
			return () => {
				document.removeEventListener('keydown', onKeyDownGlobal);
			};
		}
	}, [state?.modalCard, modalCardRef]);

	return (
		<Modal open={state?.modalCard} onCancel={handleCancel} centered width={650} footer={null}>
			<div ref={modalCardRef} autoFocus onKeyDown={onKeyDownGlobal} className="w-full h-full">
				<h1 className="text-lg font-semibold mb-5">MASUKKAN KARTU</h1>

				<Form layout="vertical" onFinish={onFinish} form={form} name="card-form">
					<Form.Item
						name="cardName"
						label={
							<p>
								Nama Kartu / Provider <kbd className="kbd">&uarr;</kbd>
							</p>
						}
						rules={[
							{
								required: true,
								message: 'Wajib Diisi!'
							}
						]}>
						<Input
							autoFocus
							ref={cardNameRef}
							placeholder="Card Holder / Name"
							value={state?.card?.name}
							onChange={(event) => {
								const value = event.target.value;

								setState((prevState) => ({
									...prevState,
									card: {
										...prevState.card,
										name: value
									}
								}));
							}}
							className="w-full h-14 rounded-md"
						/>
					</Form.Item>
					<Form.Item
						name="cardNo"
						label={
							<p>
								Nomor Kartu <kbd className="kbd">&darr;</kbd>
							</p>
						}
						rules={[
							{
								required: true,
								message: 'Wajib Diisi!'
							}
						]}>
						<Input
							ref={cardNumberRef}
							placeholder="Card Number"
							value={state?.card?.no}
							onChange={(event) => {
								const value = event.target.value;

								setState((prevState) => ({
									...prevState,
									card: {
										...prevState.card,
										no: value
									}
								}));
							}}
							className="w-full h-14 rounded-md"
						/>
					</Form.Item>
					<div className="w-full h-auto p-3 bg-gray-100 rounded-md mb-5">
						Gunakan <b>&uarr;</b> untuk mengisi <b>Nama Kartu</b>, dan <b>&darr;</b> mengisi <b>Nomor Kartu</b>
					</div>
					<Form.Item>
						<div className="w-full flex items-center justify-end gap-3">
							<Button htmlType="button" className="h-14 px-5 rounded-md uppercase" onClick={handleCancel}>
								Batalkan
							</Button>
							<Button htmlType="submit" className="btn-page h-12 w-max px-7">
								Bayar
							</Button>
						</div>
					</Form.Item>
				</Form>
			</div>
		</Modal>
	);
}
