import { Button } from 'antd';
import React, { useEffect, useRef, useState } from 'react';
import { FaRegTrashCan } from 'react-icons/fa6';
import { currencyFormat } from '../../../../utils/helper/utilities';

export default function TableBalanceOrder({ state, setState, handleFocus }) {
	const [activeIndex, setActiveIndex] = useState(-1);
	const tableRef = useRef(null);
	const tableData = state?.settles || [];

	const handleKeyDownGlobal = (e) => {
		switch (e.key) {
			case 'F4':
				setActiveIndex(0);

				e.target.blur();
				e.preventDefault();
				tableRef && tableRef.current.focus();
				break;
			case 'F3':
				tableRef && tableRef.current.blur();
				break;

			default:
				break;
		}
	};

	const handleKeyDown = (e) => {
		switch (e.key) {
			case 'ArrowUp':
				setActiveIndex((prevIndex) => Math.max(prevIndex - 1, 0));
				break;
			case 'ArrowDown':
				setActiveIndex((prevIndex) => Math.min(prevIndex + 1, tableData.length - 1));
				break;
			case 'Enter':
				const settleCode = tableData[activeIndex]?.settleCode;
				if (settleCode) {
					handleDeleteBalance(settleCode);
				}
				break;
			case 'Escape':
				e.target.blur();
				handleFocus();
				break;
			default:
				break;
		}
	};

	useEffect(() => {
		if (tableRef.current) {
			tableRef.current.focus();
		}

		document.addEventListener('keydown', handleKeyDownGlobal);

		return () => {
			document.removeEventListener('keydown', handleKeyDownGlobal);
		};
	}, []);

	useEffect(() => {
		const table = tableRef.current;
		if (table) {
			table.addEventListener('keydown', handleKeyDown);
			return () => {
				table.removeEventListener('keydown', handleKeyDown);
			};
		}
	}, [tableData, activeIndex]);

	const handleDeleteBalance = (settleCode) => {
		const settleList = state?.settles;
		setState({
			settles: settleList?.filter((item) => item.settleCode !== settleCode)
		});

		tableRef.current.focus();
		handleFocus();
	};

	return (
		<div className="w-full h-full relative">
			<h1 className="mb-3 text-lg font-semibold">
				Alokasi Pembayaran <kbd className="kbd">F4</kbd>
			</h1>
			<table ref={tableRef} tabIndex={0} className="table-fixed w-full" border>
				<thead className="w-full h-14 bg-primary sticky top-0">
					<tr>
						<th className="w-10 text-center">#</th>
						<th className="w-1/2 text-left px-3 text-white">Payment Type</th>
						<th className="w-1/2 text-right px-3 text-white">Amount</th>
					</tr>
				</thead>
				<tbody>
					{tableData.map(({ cashPay = 0, tenderName = '', settleCode = '' }, index) => (
						<tr key={settleCode} className={`w-full h-14 border border-gray-500 ${index === activeIndex ? 'bg-yellow-200' : ''}`}>
							<td className="w-10 text-center">
								<Button
									icon={<FaRegTrashCan className="text-white" />}
									type="primary"
									className="flex items-center justify-center mx-auto"
									shape="circle"
									danger
									onClick={() => handleDeleteBalance(settleCode)}
								/>
							</td>
							<td className="w-1/2 text-left px-3 text-black">{tenderName.trim().toUpperCase()}</td>
							<td className="w-1/2 text-right px-3 text-black">Rp {currencyFormat(cashPay)}</td>
						</tr>
					))}
				</tbody>
			</table>
		</div>
	);
}
